/* RESET */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* BASE */
body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  background: linear-gradient(135deg, #f0fdf4 0%, #ecfdf5 50%, #d1fae5 100%);
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow-x: hidden;
  color: #111827;
  position: relative;
}

/* ANIMATED BACKGROUND */
body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    radial-gradient(circle at 20% 80%, rgba(16, 185, 129, 0.03) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(52, 211, 153, 0.02) 0%, transparent 50%),
    radial-gradient(circle at 40% 40%, rgba(167, 243, 208, 0.025) 0%, transparent 50%);
  animation: subtleFloat 25s ease-in-out infinite;
  pointer-events: none;
  z-index: -1;
}

body::after {
  content: '';
  position: fixed;
  top: -10%;
  right: -10%;
  width: 30%;
  height: 30%;
  background: radial-gradient(circle, rgba(16, 185, 129, 0.04) 0%, transparent 70%);
  border-radius: 50%;
  animation: gentleFloat 30s ease-in-out infinite reverse;
  pointer-events: none;
  z-index: -1;
}

/* LOADING STATES */
.loading {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background: linear-gradient(135deg, #f0fdf4 0%, #ecfdf5 50%, #d1fae5 100%);
  color: #047857;
  font-size: 16px;
  font-weight: 500;
  position: relative;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
}

/* NORMAL LOADING SPINNER */
.loading::after {
  content: '';
  margin-top: 24px;
  width: 40px;
  height: 40px;
  border: 4px solid #d1fae5;
  border-top: 4px solid #10b981;
  border-radius: 50%;
  display: block;
  animation: normalSpin 1s linear infinite;
}

/* NORMAL SPINNER ANIMATION */
@keyframes normalSpin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* ENHANCED LOADING CARD */
.loading-card {
  background: linear-gradient(145deg, 
    rgba(255, 255, 255, 0.95) 0%, 
    rgba(240, 253, 244, 0.9) 40%, 
    rgba(255, 255, 255, 0.95) 100%);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-radius: 20px;
  padding: 40px 30px;
  box-shadow: 
    0 25px 50px rgba(16, 185, 129, 0.08),
    0 10px 25px rgba(0, 0, 0, 0.05),
    inset 0 1px 0 rgba(255, 255, 255, 0.8);
  border: 1.5px solid rgba(16, 185, 129, 0.12);
  display: flex;
  flex-direction: column;
  align-items: center;
  animation: loadingPulse 2s ease-in-out infinite;
  position: relative;
  overflow: hidden;
}

.loading-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, 
    transparent 0%, 
    rgba(16, 185, 129, 0.4) 50%, 
    transparent 100%);
}

@keyframes loadingPulse {
  0%, 100% {
    transform: scale(1);
    box-shadow: 
      0 25px 50px rgba(16, 185, 129, 0.08),
      0 10px 25px rgba(0, 0, 0, 0.05),
      inset 0 1px 0 rgba(255, 255, 255, 0.8);
  }
  50% {
    transform: scale(1.02);
    box-shadow: 
      0 30px 60px rgba(16, 185, 129, 0.12),
      0 15px 30px rgba(0, 0, 0, 0.08),
      inset 0 1px 0 rgba(255, 255, 255, 0.9);
  }
}

/* LOADING DOTS */
.loading-dots {
  display: flex;
  gap: 8px;
  margin-top: 20px;
}

.loading-dots::before,
.loading-dots::after {
  content: '';
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #10b981;
  animation: loadingDots 1.5s ease-in-out infinite;
}

.loading-dots::before {
  animation-delay: 0s;
}

.loading-dots::after {
  animation-delay: 0.5s;
}

@keyframes loadingDots {
  0%, 80%, 100% {
    transform: scale(0.8);
    opacity: 0.5;
  }
  40% {
    transform: scale(1.2);
    opacity: 1;
  }
}

/* CLERK ROOT CENTERING */
