/* MAIN CONTAINER */
.auth-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  background: linear-gradient(135deg, #f0fdf4, #ecfdf5 50%, #d1fae5);
  animation: fadeIn 0.8s ease-out;
  position: relative;
}

/* CARD */
.auth-card {
  background: linear-gradient(145deg, 
    rgba(255, 255, 255, 0.95) 0%, 
    rgba(240, 253, 244, 0.9) 40%, 
    rgba(255, 255, 255, 0.95) 100%);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-radius: 20px;
  padding: 32px 28px;
  width: 100%;
  max-width: 380px;
  box-shadow: 
    0 25px 50px rgba(16, 185, 129, 0.08),
    0 10px 25px rgba(0, 0, 0, 0.05),
    inset 0 1px 0 rgba(255, 255, 255, 0.8);
  display: flex;
  flex-direction: column;
  align-items: center;
  animation: slideUp 0.8s cubic-bezier(0.16, 1, 0.3, 1);
  border: 1.5px solid rgba(16, 185, 129, 0.12);
  position: relative;
  overflow: hidden;
}

/* CARD GLOW EFFECT */
.auth-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, 
    transparent 0%, 
    rgba(16, 185, 129, 0.4) 50%, 
    transparent 100%);
}

.auth-card::after {
  content: '';
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  background: linear-gradient(135deg, 
    rgba(16, 185, 129, 0.1) 0%, 
    transparent 25%, 
    transparent 75%, 
    rgba(52, 211, 153, 0.1) 100%);
  border-radius: 26px;
  z-index: -1;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.auth-card:hover::after {
  opacity: 1;
}

/* HEADER */
.auth-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 24px;
  text-align: center;
  position: relative;
}

/* LOGO */
.logo {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 12px;
  transition: transform 0.3s ease;
}

.logo:hover {
  transform: scale(1.02);
}

.logo-icon {
  width: 64px;
  height: 64px;
  background: linear-gradient(135deg, #10b981 0%, #34d399 50%, #6ee7b7 100%);
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 800;
  font-size: 20px;
  color: white;
  letter-spacing: -0.02em;
  box-shadow: 
    0 8px 24px rgba(16, 185, 129, 0.4),
    inset 0 1px 0 rgba(255, 255, 255, 0.3);
  position: relative;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.logo-icon::before {
  content: '';
  position: absolute;
  top: 2px;
  left: 2px;
  right: 2px;
  bottom: 2px;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.1) 0%, transparent 100%);
  border-radius: 18px;
  pointer-events: none;
}

.logo-icon:hover {
  transform: translateY(-2px) scale(1.05);
  box-shadow: 
    0 12px 32px rgba(16, 185, 129, 0.5),
    inset 0 1px 0 rgba(255, 255, 255, 0.4);
}

.logo h1 {
  font-size: 32px;
  font-weight: 800;
  background: linear-gradient(135deg, #10b981 0%, #059669 50%, #047857 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin: 0;
  letter-spacing: -0.02em;
  position: relative;
}

.logo h1::after {
  content: '';
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, #10b981, transparent);
  border-radius: 1px;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.logo:hover h1::after {
  opacity: 1;
}

/* SUBTITLE */
.auth-subtitle {
  font-size: 16px;
  color: #4b5563;
  line-height: 1.5;
  font-weight: 500;
  margin-bottom: 12px;
  max-width: 300px;
  position: relative;
  padding: 0 8px;
}

/* CONTENT */
.auth-content {
  width: 100%;
  margin: 12px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
  position: relative;
}

/* BUTTON CONTAINER */
.auth-content::before {
  content: '';
  position: absolute;
  top: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 2px;
  background: linear-gradient(90deg, transparent, #10b981, transparent);
  border-radius: 1px;
  opacity: 0.6;
}

/* AUTH BUTTONS */
.auth-button {
  background: linear-gradient(135deg, #10b981 0%, #34d399 50%, #6ee7b7 100%);
  border: none;
  border-radius: 16px;
  padding: 18px 36px;
  font-weight: 700;
  font-size: 17px;
  color: white;
  cursor: pointer;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  min-width: 240px;
  box-shadow: 
    0 6px 20px rgba(16, 185, 129, 0.35),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
  letter-spacing: 0.02em;
  backdrop-filter: blur(10px);
}

.auth-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, 
    transparent, 
    rgba(255, 255, 255, 0.3), 
    transparent);
  transition: left 0.6s ease;
}

.auth-button:hover::before {
  left: 100%;
}

.auth-button:hover {
  transform: translateY(-3px) scale(1.02);
  box-shadow: 
    0 12px 32px rgba(16, 185, 129, 0.45),
    inset 0 1px 0 rgba(255, 255, 255, 0.3);
  background: linear-gradient(135deg, #059669 0%, #10b981 50%, #34d399 100%);
}

.auth-button:active {
  transform: translateY(-1px) scale(1.01);
  box-shadow: 
    0 6px 20px rgba(16, 185, 129, 0.4),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

/* BUTTON VARIANTS */
.auth-button:nth-child(2) {
  background: linear-gradient(135deg, #059669 0%, #10b981 50%, #34d399 100%);
  box-shadow: 
    0 6px 20px rgba(5, 150, 105, 0.35),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.auth-button:nth-child(2):hover {
  background: linear-gradient(135deg, #047857 0%, #059669 50%, #10b981 100%);
  box-shadow: 
    0 12px 32px rgba(5, 150, 105, 0.45),
    inset 0 1px 0 rgba(255, 255, 255, 0.3);
}

/* SECONDARY STYLING FOR SIGN UP */
.auth-content a:last-child .auth-button {
  background: linear-gradient(135deg, 
    rgba(255, 255, 255, 0.9) 0%, 
    rgba(240, 253, 244, 0.8) 50%, 
    rgba(255, 255, 255, 0.9) 100%);
  color: #059669;
  border: 2px solid rgba(16, 185, 129, 0.3);
  backdrop-filter: blur(10px);
  box-shadow: 
    0 4px 15px rgba(16, 185, 129, 0.15),
    inset 0 1px 0 rgba(255, 255, 255, 0.8);
}

.auth-content a:last-child .auth-button:hover {
  background: linear-gradient(135deg, #10b981 0%, #34d399 50%, #6ee7b7 100%);
  color: white;
  border-color: transparent;
  transform: translateY(-3px) scale(1.02);
  box-shadow: 
    0 12px 32px rgba(16, 185, 129, 0.4),
    inset 0 1px 0 rgba(255, 255, 255, 0.3);
}

/* Remove default link styling */
a {
  text-decoration: none;
}

/* FOOTER */
.auth-footer {
  margin-top: 20px;
  padding-top: 16px;
  border-top: 1px solid rgba(16, 185, 129, 0.15);
  text-align: center;
  width: 100%;
}

.auth-footer p {
  font-size: 13px;
  color: #6b7280;
  font-weight: 500;
}

/* ANIMATIONS */
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes slideUp {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

/* BACKGROUND ANIMATIONS */
@keyframes subtleFloat {
  0%, 100% {
    transform: translate(0, 0) rotate(0deg);
    opacity: 1;
  }
  25% {
    transform: translate(2%, -1%) rotate(0.5deg);
    opacity: 0.8;
  }
  50% {
    transform: translate(-1%, 2%) rotate(-0.3deg);
    opacity: 0.9;
  }
  75% {
    transform: translate(-2%, -1%) rotate(0.2deg);
    opacity: 0.85;
  }
}

@keyframes gentleFloat {
  0%, 100% {
    transform: translate(0, 0) scale(1);
  }
  33% {
    transform: translate(-3%, 2%) scale(1.02);
  }
  66% {
    transform: translate(2%, -2%) scale(0.98);
  }
}

/* RESPONSIVE DESIGN */
@media (max-width: 640px) {
  .auth-container {
    padding: 16px;
  }
  
  .auth-card {
    padding: 36px 28px;
    max-width: 380px;
  }
  
  .logo-icon {
    width: 56px;
    height: 56px;
    font-size: 18px;
  }
  
  .logo h1 {
    font-size: 26px;
  }
  
  .auth-button {
    min-width: 200px;
    padding: 16px 28px;
    font-size: 16px;
  }
  
  .auth-header {
    margin-bottom: 32px;
  }
  
  .auth-subtitle {
    font-size: 15px;
  }
}

@media (max-width: 480px) {
  .auth-container {
    padding: 12px;
  }
  
  .auth-card {
    padding: 28px 24px;
    max-width: 100%;
    border-radius: 20px;
  }
  
  .logo {
    gap: 12px;
  }
  
  .logo-icon {
    width: 48px;
    height: 48px;
    font-size: 16px;
    border-radius: 16px;
  }
  
  .logo h1 {
    font-size: 22px;
  }
  
  .auth-button {
    min-width: 180px;
    padding: 14px 24px;
    font-size: 15px;
    border-radius: 14px;
  }
  
  .auth-header {
    margin-bottom: 28px;
  }
  
  .auth-content {
    gap: 16px;
  }
}

/* ACCESSIBILITY */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}

/* FOCUS STATES */
.auth-button:focus {
  outline: 2px solid #10b981;
  outline-offset: 2px;
}
