/* FLOATING SHAPES CONTAINER */
.floating-shapes-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: -1;
  overflow: hidden;
}

/* BASE FLOATING SHAPE */
.floating-shape {
  position: absolute;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

/* CIRCLE SHAPES */
.floating-shape.circle {
  border-radius: 50%;
  background: linear-gradient(135deg, 
    rgba(16, 185, 129, 0.15) 0%, 
    rgba(52, 211, 153, 0.1) 50%, 
    rgba(167, 243, 208, 0.05) 100%);
  animation-name: floatCircle;
  border: 1px solid rgba(16, 185, 129, 0.2);
  backdrop-filter: blur(1px);
}

/* SQUARE SHAPES */
.floating-shape.square {
  background: linear-gradient(45deg, 
    rgba(34, 197, 94, 0.12) 0%, 
    rgba(74, 222, 128, 0.08) 100%);
  border-radius: 8px;
  animation-name: floatSquare;
  border: 1px solid rgba(34, 197, 94, 0.15);
  transform-origin: center;
}

/* TRIANGLE SHAPES */
.floating-shape.triangle {
  width: 0 !important;
  height: 0 !important;
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;
  border-bottom: 35px solid rgba(16, 185, 129, 0.1);
  animation-name: floatTriangle;
  filter: drop-shadow(0 2px 4px rgba(16, 185, 129, 0.1));
}

/* HEXAGON SHAPES */
.floating-shape.hexagon {
  background: rgba(5, 150, 105, 0.08);
  position: relative;
  animation-name: floatHexagon;
  clip-path: polygon(30% 0%, 70% 0%, 100% 50%, 70% 100%, 30% 100%, 0% 50%);
  border: 1px solid rgba(5, 150, 105, 0.15);
}

/* BUBBLE EFFECTS */
.bubble {
  position: absolute;
  border-radius: 50%;
  background: radial-gradient(circle at 30% 30%, 
    rgba(255, 255, 255, 0.8) 0%, 
    rgba(16, 185, 129, 0.3) 30%, 
    rgba(16, 185, 129, 0.1) 70%, 
    transparent 100%);
  border: 2px solid rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(2px);
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
}

.bubble-1 {
  width: 120px;
  height: 120px;
  top: 10%;
  left: 15%;
  animation: bubbleFloat1 18s infinite;
}

.bubble-2 {
  width: 80px;
  height: 80px;
  top: 70%;
  right: 20%;
  animation: bubbleFloat2 22s infinite;
}

.bubble-3 {
  width: 60px;
  height: 60px;
  top: 30%;
  right: 10%;
  animation: bubbleFloat3 16s infinite;
}

.bubble-4 {
  width: 100px;
  height: 100px;
  bottom: 20%;
  left: 8%;
  animation: bubbleFloat4 20s infinite;
}

.bubble-5 {
  width: 40px;
  height: 40px;
  top: 50%;
  left: 80%;
  animation: bubbleFloat5 14s infinite;
}

/* PARTICLES */
.particles {
  position: absolute;
  width: 100%;
  height: 100%;
}

.particle {
  position: absolute;
  width: 4px;
  height: 4px;
  background: rgba(16, 185, 129, 0.4);
  border-radius: 50%;
  animation: particleFloat infinite linear;
  box-shadow: 0 0 6px rgba(16, 185, 129, 0.3);
}

/* ANIMATIONS */

/* Circle Animation */
@keyframes floatCircle {
  0% {
    transform: translateY(100vh) translateX(0px) rotate(0deg) scale(0.8);
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    transform: translateY(-20vh) translateX(30px) rotate(180deg) scale(1.2);
    opacity: 0;
  }
}

/* Square Animation */
@keyframes floatSquare {
  0% {
    transform: translateY(100vh) translateX(0px) rotate(0deg) scale(1);
    opacity: 0;
  }
  15% {
    opacity: 1;
  }
  85% {
    opacity: 1;
  }
  100% {
    transform: translateY(-20vh) translateX(-40px) rotate(90deg) scale(0.6);
    opacity: 0;
  }
}

/* Triangle Animation */
@keyframes floatTriangle {
  0% {
    transform: translateY(100vh) translateX(0px) rotate(0deg) scale(1);
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  80% {
    opacity: 1;
  }
  100% {
    transform: translateY(-20vh) translateX(50px) rotate(120deg) scale(1.5);
    opacity: 0;
  }
}

/* Hexagon Animation */
@keyframes floatHexagon {
  0% {
    transform: translateY(100vh) translateX(0px) rotate(0deg) scale(0.9);
    opacity: 0;
  }
  12% {
    opacity: 1;
  }
  88% {
    opacity: 1;
  }
  100% {
    transform: translateY(-20vh) translateX(-25px) rotate(60deg) scale(1.1);
    opacity: 0;
  }
}

/* Bubble Animations */
@keyframes bubbleFloat1 {
  0%, 100% {
    transform: translateY(0px) translateX(0px) scale(1);
  }
  25% {
    transform: translateY(-20px) translateX(10px) scale(1.1);
  }
  50% {
    transform: translateY(-30px) translateX(-15px) scale(0.9);
  }
  75% {
    transform: translateY(-10px) translateX(20px) scale(1.05);
  }
}

@keyframes bubbleFloat2 {
  0%, 100% {
    transform: translateY(0px) translateX(0px) scale(1) rotate(0deg);
  }
  33% {
    transform: translateY(15px) translateX(-12px) scale(1.15) rotate(120deg);
  }
  66% {
    transform: translateY(-25px) translateX(8px) scale(0.85) rotate(240deg);
  }
}

@keyframes bubbleFloat3 {
  0%, 100% {
    transform: translateY(0px) translateX(0px) scale(1);
  }
  40% {
    transform: translateY(-18px) translateX(-20px) scale(1.2);
  }
  80% {
    transform: translateY(12px) translateX(15px) scale(0.95);
  }
}

@keyframes bubbleFloat4 {
  0%, 100% {
    transform: translateY(0px) translateX(0px) scale(1) rotate(0deg);
  }
  20% {
    transform: translateY(-15px) translateX(25px) scale(1.08) rotate(45deg);
  }
  60% {
    transform: translateY(20px) translateX(-18px) scale(0.92) rotate(-30deg);
  }
  90% {
    transform: translateY(-8px) translateX(12px) scale(1.03) rotate(15deg);
  }
}

@keyframes bubbleFloat5 {
  0%, 100% {
    transform: translateY(0px) translateX(0px) scale(1);
  }
  30% {
    transform: translateY(-22px) translateX(-8px) scale(1.25);
  }
  70% {
    transform: translateY(18px) translateX(22px) scale(0.8);
  }
}

/* Particle Animation */
@keyframes particleFloat {
  0% {
    transform: translateY(100vh) translateX(0px);
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    transform: translateY(-10vh) translateX(100px);
    opacity: 0;
  }
}

/* PULSE ANIMATION FOR INTERACTIVE ELEMENTS */
@keyframes pulse {
  0%, 100% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.05);
    opacity: 0.8;
  }
}

/* GLOW ANIMATION */
@keyframes glow {
  0%, 100% {
    box-shadow: 0 0 5px rgba(16, 185, 129, 0.3);
  }
  50% {
    box-shadow: 0 0 20px rgba(16, 185, 129, 0.6), 0 0 30px rgba(16, 185, 129, 0.4);
  }
}

/* ADDITIONAL SHAPE VARIANTS */
.floating-shape.circle:nth-child(even) {
  background: linear-gradient(135deg, 
    rgba(5, 150, 105, 0.12) 0%, 
    rgba(16, 185, 129, 0.08) 50%, 
    rgba(52, 211, 153, 0.06) 100%);
  animation-name: floatCircleReverse;
}

.floating-shape.square:nth-child(odd) {
  background: linear-gradient(-45deg, 
    rgba(34, 197, 94, 0.1) 0%, 
    rgba(74, 222, 128, 0.06) 100%);
  animation-name: floatSquareReverse;
}

/* REVERSE ANIMATIONS */
@keyframes floatCircleReverse {
  0% {
    transform: translateY(-20vh) translateX(0px) rotate(180deg) scale(1.2);
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    transform: translateY(100vh) translateX(-30px) rotate(0deg) scale(0.8);
    opacity: 0;
  }
}

@keyframes floatSquareReverse {
  0% {
    transform: translateY(-20vh) translateX(0px) rotate(90deg) scale(0.6);
    opacity: 0;
  }
  15% {
    opacity: 1;
  }
  85% {
    opacity: 1;
  }
  100% {
    transform: translateY(100vh) translateX(40px) rotate(0deg) scale(1);
    opacity: 0;
  }
}

/* RESPONSIVE ADJUSTMENTS */
@media (max-width: 768px) {
  .floating-shape {
    transform: scale(0.7);
  }
  
  .bubble {
    transform: scale(0.8);
  }
  
  .particle {
    transform: scale(0.5);
  }
}

@media (max-width: 480px) {
  .floating-shape {
    transform: scale(0.5);
  }
  
  .bubble {
    transform: scale(0.6);
  }
}

/* ACCESSIBILITY - RESPECT REDUCED MOTION PREFERENCE */
@media (prefers-reduced-motion: reduce) {
  .floating-shape,
  .bubble,
  .particle {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
  }
}

/* DARK MODE SUPPORT */
@media (prefers-color-scheme: dark) {
  .floating-shape.circle {
    background: linear-gradient(135deg, 
      rgba(16, 185, 129, 0.08) 0%, 
      rgba(52, 211, 153, 0.05) 50%, 
      rgba(167, 243, 208, 0.03) 100%);
  }
  
  .floating-shape.square {
    background: linear-gradient(45deg, 
      rgba(34, 197, 94, 0.06) 0%, 
      rgba(74, 222, 128, 0.04) 100%);
  }
  
  .bubble {
    background: radial-gradient(circle at 30% 30%, 
      rgba(255, 255, 255, 0.1) 0%, 
      rgba(16, 185, 129, 0.15) 30%, 
      rgba(16, 185, 129, 0.05) 70%, 
      transparent 100%);
  }
}
